@import '../../style/variables';

.header {
  position: sticky;
  top: 0px;
  height: 96px;
  padding: 32px $section_padding 24px;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: $color_white;
  box-shadow: 0px 120px 80px 60px rgba(0, 0, 0, 0.4);

  @media screen and (max-width: $tablet_width) {
    padding: 32px $section_padding_mobile 24px;
    justify-content: flex-end;
    height: 88px;

    div:nth-child(1) {
      justify-self: flex-start;
      margin-right: auto;
    }
  }
}
